import React from 'react';
import {
    Group,
    Div,
    File,
    FormLayout,
    CardGrid,
    Card,
    Snackbar,
    Cell,
    PanelSpinner,
    Spinner,
    CellButton
} from "@vkontakte/vkui"
import {BACKEND} from '../func/func.js';
import Icon24Camera from '@vkontakte/icons/dist/24/camera';
import bridge from '@vkontakte/vk-bridge';
import fetchJsonp from "fetch-jsonp";
import {connect} from "react-redux";
import Icon24Gallery from '@vkontakte/icons/dist/24/gallery';

class Portfolio extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            activeMaster: {},
            photoArr: [],
            isLoad: false,
            photoFile: '',
            images: []
        };
    }

    componentDidMount() {
        this.getToken();
        this.setState({images: this.props.master.photos, isLoad: true});
    }

    openSnack(text) {
        if (this.state.snackbar) return;
        this.setState({
            snackbar:
                <Snackbar
                    layout="vertical"
                    onClose={() => this.setState({snackbar: null})}
                >
                    {text}
                </Snackbar>
        });
    }

    openShowImages(images, index) {
        bridge.send("VKWebAppShowImages", {
            images: images,
            start_index: index
        }).then(data => console.log(data));
    }

    async getAlbum() {
        return await fetch(BACKEND.vkapi.getAlbum, {
            method: 'POST', // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, cors, *same-origin
            cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
            credentials: 'same-origin', // include, *same-origin, omit
            headers: {'Content-Type': 'application/json',},
            redirect: 'follow', // manual, *follow, error
            referrer: 'no-referrer', // no-referrer, *client
            body: JSON.stringify({params: this.props.params}), //    body     "Content-Type"
        })
            .then(res => {
                console.log(res)
                if (!res.album_id) {
                    return {status: 'none'}
                } else {
                    return {status: true, album_id: res.album_id}
                }
            })
            .catch(e => {
                console.log(e);
                return {status: false}
            })
    };

    uploadPhoto = async () => {
        try {
            let selectedFile = document.getElementById('input').files[0];
            if (!selectedFile.type.match('image.*')) throw '   .';
            let getAlbum = await this.getAlbum();
            let album;
            if (getAlbum.status === false) throw '    .';
            if (getAlbum.status === 'none') {
                //
                const create_album = encodeURI('https://api.vk.com/method/photos.createAlbum?title= &access_token=' + this.state.token + '&v=5.103');
                await fetchJsonp(create_album)
                    .then(res => res.json())
                    .then(res => {
                        console.log(res);
                        album = res.response.id;
                    })
                    .catch(e => console.log(e))
            } else {
                album = getAlbum.album_id;
            }
            const uploadServer = await this.getUploadServer(this.state.token, album);

            if (uploadServer.status === false) throw '.      .';
            if (this.state.uploadUrl === undefined) throw '.      .';
            if (this.state.token === undefined) throw '.    .';
            this.setState({loading: true});
            const formData = new FormData();
            formData.append('master', this.props.master.firstname + ' ' + this.props.master.lastname);
            formData.append('uploadUrl', uploadServer.url);
            formData.append('token', this.state.token);
            formData.append('file1', selectedFile);
            formData.append('masterId', this.props.master._id);
            fetch(BACKEND.vkapi.uploadPhoto, {
                method: 'POST',
                body: formData
            })
                .then(res => res.json())
                .then(res => {
                    if (res.error) {
                        this.setState({loading: false});
                        this.openSnack(res.error);
                    } else {
                        fetchJsonp(res.saveUrl, {
                            mode: 'no-cors',
                            method: 'GET'
                        })
                            .then(result => result.json())
                            .then(result => {
                                console.log('save',result);
                                const savePh = {
                                    url: result.response[0].sizes[2].url,
                                    params: this.props.params
                                };
                                fetch(BACKEND.vkapi.savePhoto, {
                                    method: 'POST', // *GET, POST, PUT, DELETE, etc.
                                    mode: 'cors', // no-cors, cors, *same-origin
                                    cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
                                    credentials: 'same-origin', // include, *same-origin, omit
                                    headers: {
                                        'Content-Type': 'application/json',
                                    },
                                    redirect: 'follow', // manual, *follow, error
                                    referrer: 'no-referrer', // no-referrer, *client
                                    body: JSON.stringify(savePh)
                                })
                                    .then(res => res.json())
                                    .then(res => {
                                        if (res.error) {
                                            this.setState({loading: false});
                                            this.openSnack(res.error);
                                        } else {
                                            let imgArr = this.state.images;
                                            imgArr.push(res.img);
                                            this.openSnack('  .');
                                            this.setState({images: imgArr, loading: false});
                                        }
                                    })
                                    .catch(e => {
                                        this.setState({loading: false});
                                        console.log(e)
                                    })
                            })
                            .catch(e => {
                                this.setState({loading: false});
                                console.log(e)
                            })
                        // console.log(res);
                        // let imgArr = this.state.images;
                        // imgArr.unshift(res.img);
                        // this.openSnack('  ');
                        // this.setState({images: imgArr, loading: false});
                    }
                })
                .catch(error => {
                    console.log(error)
                    this.setState({loading: false});
                    this.openSnack(error);
                })
        } catch (e) {
            console.log(e);
            this.openSnack('- .   ?');
        }

    };

    getUploadServer = async (token, album) => {
        return await bridge.send("VKWebAppCallAPIMethod", {
            "method": "photos.getUploadServer",
            "params": {"album_id": album, "v": "5.103", "access_token": token}
        })
            .then(result => {
                console.log(result);
                this.setState({uploadUrl: result.response.upload_url});
                return {status: true, url: result.response.upload_url};
            })
            .catch(e => {
                console.log(e)
                return {status: false}
            })

    };

    getToken = () => {
        bridge.send("VKWebAppGetAuthToken", {"app_id": 7170938, "scope": "photos"})
            .then(result => {
                if (result.access_token) {
                    this.setState({token: result.access_token})
                    //this.getUploadServer(result.access_token)
                    //return result.access_token //this.getUploadServer(data.access_token);
                }
                // if (result.type === 'VKWebAppGetAuthTokenResult'){
                //     this.setState({token: result.data.access_token})
                //     return result.data.access_token //this.getUploadServer(data.access_token);
                // }
            })
            .catch(error => console.log(error))
    };

    gridPhoto() {
        if (this.state.images.length > 0) {
            return (
                <CardGrid>
                    {
                        this.state.images.map((image, index) => {
                            return (
                                <Card
                                    style={{padding: 2, borderRadius: 13, margin: 0}}
                                    size="s"
                                    mode="shadow"
                                    key={index}
                                    onClick={() => this.openShowImages(this.state.images, index)}
                                >
                                    <div style={{
                                        height: 96,
                                        backgroundImage: 'url(' + image + ')',
                                        backgroundSize: 'cover',
                                        borderRadius: 13
                                    }}/>
                                </Card>
                            )
                        })
                    }
                </ CardGrid>
            )
        } else {
            return (<Cell>    </Cell>)
        }
    }

    render() {
        if (this.state.isLoad === false) {
            return (
                <Spinner size="large" style={{marginTop: 20}}/>
            )
        } else {
            return (
                <Div>
                    {
                        this.state.loading ? <Div><Cell multiline> ... 
                            </Cell><PanelSpinner/></Div> : <Group title="">
                            <FormLayout>
                                {
                                    this.state.token ?
                                        <File
                                            accept="image/*"
                                            top="   "
                                            before={<Icon24Camera/>}
                                            size="l"
                                            onChange={this.uploadPhoto}
                                            //onClick={this.getToken}
                                            id="input"
                                        >
                                             
                                        </File> :
                                        <CellButton onClick={() => this.getToken()} before={<Icon24Gallery/>}>
                                              </CellButton>

                                }
                            </FormLayout>
                            <Group separator="hide">
                                {this.gridPhoto()}
                            </Group>
                        </Group>
                    }
                    {this.state.snackbar}
                </Div>
            )
        }
    }
}

const putStateToProps = (state) => {
    return {
        user: state.user,
        master: state.master,
        params: state.params
    };
};

const putActionsToProps = (dispatch) => {
    return {};
};

export default connect(putStateToProps, putActionsToProps)(Portfolio);